body{
  margin: 0;
  border: 0;
}

.section1, .section2, .section3 {
  display: flex;
  min-width: 99vw;
  min-height: 110vh;
  align-items: center;
  justify-content: center;
}

.section1 {
  flex-direction: column;
}

.download_links {
  display: flex;
}

.section1{
  background-image: url('../annie-spratt-6a3nqQ1YwBw-unsplash.jpg');
}

.white_text { 
  color: wheat;
}

.section2{
  flex-direction: column;
  background-color: rgb(32, 26, 32);
}

.Projects_Intro{
  font-size: x-large;
}

.section3{
  background-image: url('../annie-spratt-6a3nqQ1YwBw-unsplash.jpg');
}

#contact-form { 
  display: flex;
  flex-direction: column;
}

.Header {
  display: flex;
  justify-content: space-between;
  position: fixed; 
  top: 0;
  width: 100%;
}

.Links {
  display: flex;
}

.Links > h4 {
  margin-right: 5px;
}

.Header_Title{
  margin-left: 5px;
}

.Project_Card {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1;
  margin: 0 20px;
  background-color: bisque;
}

.Project_Container {
  display: flex;
}